.Movie {
  width: 100%;
  overflow: hidden;
  background-color: hsl(228, 100%, 99%);
  min-height: 11.5em;
  transition: all 0.4s ease;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  border-radius: 15px;
}

.Movie:hover {
  transform: translateY(-10px);
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
    rgba(0, 0, 0, 0.22) 0px 15px 12px;
}

.Movie img {
  width: 7em;
  position: absolute;
  transform: translate(1.225em, 1.225em);
  box-shadow: 0.3125em 0.625em 0.3125em rgba(51, 51, 51, 0.329);
}

.Wrap {
  padding: 0.625em 0.625em 0.625em 9.5625em;
}

.Title {
  font-size: 1.125em;
  font-weight: 700;
  color: #755139;
  margin-bottom: 0.125em;
}

.Genre {
  font-size: 1em;
  color: #a7afbd;
  margin-left: -0.3125em;
}

.Summary {
  font-size: 1rem;
  line-height: 1.2rem;
  color: #4465a3;
  margin: 0.8rem 0;
}
